import { config } from '@/checkout/api';
import { estimateShippingMethodsMutation } from '@/checkout/api/estimateShippingMethods/graphql';
import { ShippingMethod } from '@/checkout/data/models';
import {
  transformShippingEstimatePartialAddress,
  transformShippingEstimateShippingMethod,
  transformShippingMethods,
} from '@/checkout/data/transforms';
import { dispatchApiCall, getValue, QueueName, state } from '@/checkout/lib';
import { MissingCart, MissingCountry } from '@/checkout/lib/errors';
import { events } from '@adobe-commerce/event-bus';

export interface ShippingEstimationCriteria {
  country_code: string;
  region_name?: string;
  region_id?: string | number;
  zip?: string;
}

export type EstimateShippingInput = {
  criteria: ShippingEstimationCriteria;
};

export const estimateShippingMethods = async (
  input?: EstimateShippingInput
): Promise<ShippingMethod[] | undefined> => {
  const cartId = state.cartId;
  const countryCode =
    input?.criteria?.country_code ?? state.config?.defaultCountry;

  if (!cartId) throw new MissingCart();
  if (!countryCode) throw new MissingCountry();

  const { region_id, region_name, zip } = input?.criteria ?? {};

  // Process region information
  const region =
    region_id || region_name
      ? {
          region_id:
            typeof region_id === 'string' ? parseInt(region_id, 10) : region_id,
          region_code: region_name,
        }
      : undefined;

  // Build address for API call
  const address = {
    country_code: countryCode,
    ...(zip && { postcode: zip }),
    ...(region && {
      region: {
        ...(region.region_id && { region_id: region.region_id }),
        ...(region.region_code && { region_code: region.region_code }),
      },
    }),
  };

  // Create address data for transformation
  const estimateShippingAddressData = {
    country_id: countryCode,
    region: address.region?.region_code,
    region_id: address.region?.region_id,
    postcode: zip,
  };

  const partialAddress = transformShippingEstimatePartialAddress(
    estimateShippingAddressData
  );

  return dispatchApiCall({
    options: { variables: { cartId, address } },
    path: 'estimateShippingMethods',
    query: estimateShippingMethodsMutation,
    queueName: QueueName.ShippingEstimate,
    transformer: transformShippingMethods,
    type: 'mutation',
  })
    .then((estimation) => {
      const { defaults, shipping } = config.getConfig();

      const filteredOptions = shipping?.filterOptions
        ? estimation.filter(shipping.filterOptions)
        : estimation;

      const hasAvailableOptions = filteredOptions.length > 0;
      const partialAddress = transformShippingEstimatePartialAddress(
        estimateShippingAddressData
      );

      let shippingMethod = null;

      if (hasAvailableOptions) {
        const userSelection = getValue('selectedShippingMethod');

        let selection = estimation.find(
          (method) =>
            method.code === userSelection?.code &&
            method.carrier.code === userSelection?.carrier.code
        );

        if (!selection && defaults?.selectedShippingMethod) {
          selection = defaults.selectedShippingMethod(estimation) ?? undefined;
        }

        if (!selection) {
          selection = estimation[0];
        }

        shippingMethod = transformShippingEstimateShippingMethod(selection);
      }

      events.emit('shipping/estimate', {
        address: partialAddress,
        availableShippingMethods: estimation,
        shippingMethod,
        success: true,
      });

      return filteredOptions;
    })
    .catch((error) => {
      events.emit('shipping/estimate', {
        address: partialAddress,
        shippingMethod: null,
        availableShippingMethods: [],
        success: false,
      });

      throw error;
    });
};
