import { config } from '@/checkout/api';
import {
  setShippingAddressOnCartAndUseAsBillingMutation,
  setShippingAddressOnCartMutation,
  setShippingAddressOnQuoteAndUseAsBillingMutation,
  setShippingAddressOnQuoteMutation,
} from '@/checkout/api/setShippingAddress/graphql';
import { ShippingAddressInput as ShippingAddressInputModel } from '@/checkout/data/models';
import {
  transformCart,
  transformNegotiableQuote,
  transformShippingAddressInputModelToCartInput,
  transformShippingAddressInputModelToQuoteInput,
} from '@/checkout/data/transforms';
import { dispatchApiCall, getValue, QueueName } from '@/checkout/lib';
import { InvalidInput, MissingShippingAddress } from '@/checkout/lib/errors';
import { ensureHasCartOrQuoteId } from '@/checkout/lib/guards';
import { state } from '@/checkout/lib/state';

const ensureIsValidCartInput = ({
  address,
  customerAddressId,
  customerAddressUid,
  pickupLocationCode,
}: ShippingAddressInputModel) => {
  if (!customerAddressId && customerAddressUid) {
    throw new InvalidInput('customerAddressUid is not supported');
  }

  if (!customerAddressId && !pickupLocationCode && !address) {
    throw new MissingShippingAddress();
  }
};

const ensureIsValidQuoteInput = ({
  address,
  customerAddressId,
  customerAddressUid,
  pickupLocationCode,
}: ShippingAddressInputModel) => {
  if (pickupLocationCode) {
    throw new InvalidInput('pickup location is not supported in quotes');
  }

  if (!customerAddressUid && customerAddressId) {
    throw new InvalidInput('customerAddressId is not supported in quotes');
  }

  if (!customerAddressUid && !address) {
    throw new MissingShippingAddress();
  }
};

const ensureIsValidInput = (input: ShippingAddressInputModel) => {
  const isCart = Boolean(state.cartId);
  const isQuote = Boolean(state.quoteId);

  if (isCart) {
    ensureIsValidCartInput(input);
  } else if (isQuote) {
    ensureIsValidQuoteInput(input);
  }
};

const createShippingAddressSetter = <TTransformedInput, TTransformedOutput>(
  entityId: string,
  entityKey: string,
  mutation: string,
  transformer: (data: any) => TTransformedOutput,
  transformInput: (input: ShippingAddressInputModel) => TTransformedInput,
  path: string
) => {
  return async (
    input: ShippingAddressInputModel
  ): Promise<TTransformedOutput> => {
    return await dispatchApiCall({
      type: 'mutation',
      query: mutation,
      options: {
        variables: {
          [entityKey]: entityId,
          shippingAddress: transformInput(input),
        },
      },
      path,
      queueName: QueueName.Updates,
      transformer,
    });
  };
};

export const setShippingAddress = async (input: ShippingAddressInputModel) => {
  ensureHasCartOrQuoteId();
  ensureIsValidInput(input);

  const { defaults } = config.getConfig();

  const isBillToShipping =
    getValue('isBillToShipping') ?? defaults?.isBillToShipping;

  const isCart = Boolean(state.cartId);

  const setter = isCart
    ? createShippingAddressSetter(
        state.cartId!,
        'cartId',
        isBillToShipping
          ? setShippingAddressOnCartAndUseAsBillingMutation
          : setShippingAddressOnCartMutation,
        transformCart,
        transformShippingAddressInputModelToCartInput,
        isBillToShipping
          ? 'setBillingAddressOnCart.cart'
          : 'setShippingAddressesOnCart.cart'
      )
    : createShippingAddressSetter(
        state.quoteId!,
        'quoteId',
        isBillToShipping
          ? setShippingAddressOnQuoteAndUseAsBillingMutation
          : setShippingAddressOnQuoteMutation,
        transformNegotiableQuote,
        transformShippingAddressInputModelToQuoteInput,
        isBillToShipping
          ? 'setNegotiableQuoteBillingAddress.quote'
          : 'setNegotiableQuoteShippingAddress.quote'
      );

  return await setter(input);
};
